import React from "react";
export const GameStatus = ({ turn, winner }) => {
    if (winner === "draw")
        return React.createElement("span", { className: "text-orange-600 font-bold" }, "Draw!");
    if (winner)
        return React.createElement("span", { className: "text-green-600 font-bold" },
            "Winner: ",
            winner,
            "!");
    return React.createElement("span", null,
        "Current Turn: ",
        turn);
};
