export const fetchResults = async () => {
    const response = await fetch("/results");
    if (response.ok) {
        return (await response.json());
    }
};
export const submitWin = async (result) => {
    const response = await fetch("/results", {
        method: "PATCH",
        body: JSON.stringify(result),
        headers: {
            "Content-Type": "application/json",
        },
    });
    if (response.ok) {
        return (await response.json());
    }
};
