import React, { useCallback, useEffect, useState } from "react";
import { ResultsCard } from "./results";
import Game from "./game";
import { fetchResults } from "../utils/apiClient";
export const Layout = ({}) => {
    const [results, setResults] = useState(null);
    const [err, setErr] = useState(false);
    const refreshResults = useCallback(async () => {
        const response = await fetchResults();
        if (response) {
            setResults(response);
            setErr(false);
        }
        else
            setErr(true);
    }, []);
    useEffect(() => {
        refreshResults();
    }, []);
    return (React.createElement("div", { className: "flex flex-col mt-10 items-center gap-10" },
        React.createElement("div", { className: "font-bold text-2xl" }, "Tic Tac Toe"),
        React.createElement("div", { className: "flex flex-col-reverse gap-2 md:flex-row" },
            React.createElement(ResultsCard, { results: results, err: err }),
            React.createElement(Game, { onGameEnd: refreshResults }))));
};
